import React from 'react';
import { Switch } from 'react-router-dom';
import { Route } from 'react-router-dom/cjs/react-router-dom.min';
import AuthProtectedRoutes from '../components/molecules/Routers/AuthProtectedRoutes';
import ProtectedRoutes from '../components/molecules/Routers/ProtectedRoutes';
import {
  AccountDetailsScreen, AddFamilyMemberScreen, Address, Allocation, BankDetails, ChangePasswordScreen, ChangePINScreen,
  DashScreen, DocUpload, EmailScreen, EnterPassScreen, FAddress, FAQScreen, Fatca, FundScreen, GoalScreen, GuardianDetails,
  GuardianPan, Home, ImportPortfolioScreen, InvestmentHorizon, InvestScreen, MenuScreen, MintboxScreen, ModeOfInvestment,
  NewPasswordScreen, Nominee, NotificationScreen, OTPScreen, PanScreen, PersonalDetails, PortfolioScreen, PurposeOfInvestment,
  ResetPasswordScreen, SetupPasswordScreen, SignScreen, SignupScreen, TransactionsScreen, WatchlistScreen
} from '../screens';



const HomeLayout = () => {
  return (
    <Switch>

      {/* -------- Route is for MarketingScreens -------- */}
      <Route exact path={`/`} component={Home} />

      {/* -------- AuthProtectedRoutes is for all AuthScreens -------- */}
      <AuthProtectedRoutes path={`/email`} component={EmailScreen} />
      <AuthProtectedRoutes path={`/login`} component={EnterPassScreen} />
      <AuthProtectedRoutes path={`/signup`} component={SignupScreen} />
      <AuthProtectedRoutes path={`/otp_screen`} component={OTPScreen} />
      <AuthProtectedRoutes path={`/setup_pass`} component={SetupPasswordScreen} />
      <AuthProtectedRoutes exact path={`/reset_pass`} component={ResetPasswordScreen} />
      <AuthProtectedRoutes path={`/reset_pass/:email/:token/:clientId`} component={NewPasswordScreen} />

      {/* -------- ProtectedRoutes is for all Mintbox Screens -------- */}
      <ProtectedRoutes exact path={`/dashboard`} component={DashScreen} />

      {/* Portfolio Specific Routes */}
      <ProtectedRoutes exact path={`/portfolio`} component={PortfolioScreen} />
      <ProtectedRoutes exact path={`/portfolio/fundScreen`} component={FundScreen} />
      <ProtectedRoutes exact path={`/portfolio/mintboxScreen`} component={MintboxScreen} />
      <ProtectedRoutes exact path={`/portfolio/goalScreen`} component={GoalScreen} />

      {/* Menu Specific Routes */}
      <ProtectedRoutes exact path={`/menu`} component={MenuScreen} />
      <ProtectedRoutes exact path={`/menu/transactions`} component={TransactionsScreen} />
      <ProtectedRoutes exact path={`/menu/import-portfolio`} component={ImportPortfolioScreen} />
      <ProtectedRoutes exact path={`/menu/add-family-member`} component={AddFamilyMemberScreen} />
      <ProtectedRoutes exact path={`/menu/notifications`} component={NotificationScreen} />
      <ProtectedRoutes exact path={`/menu/watchlist`} component={WatchlistScreen} />
      <ProtectedRoutes exact path={`/menu/faqs`} component={FAQScreen} />
      <ProtectedRoutes exact path={`/menu/change-password`} component={ChangePasswordScreen} />
      <ProtectedRoutes exact path={`/menu/change-pin`} component={ChangePINScreen} />
      <ProtectedRoutes exact path={`/menu/account`} component={AccountDetailsScreen} />

      {/* Complete Profile Routes */}
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/pan`} component={PanScreen} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/personal_details`} component={PersonalDetails} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/address`} component={Address} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/fatca`} component={Fatca} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/nominee`} component={Nominee} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/guardian_pan`} component={GuardianPan} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/guardian_details`} component={GuardianDetails} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/faddress`} component={FAddress} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/allocation`} component={Allocation} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/bank`} component={BankDetails} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/doc`} component={DocUpload} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/sign`} component={SignScreen} />


      {/* Invest Routes */}
      <ProtectedRoutes bottom_tab={false} exact path={`/invest`} component={InvestScreen} />
      <ProtectedRoutes bottom_tab={false} exact path={`/invest/purpose_of_investment`} component={PurposeOfInvestment} />
      <ProtectedRoutes bottom_tab={false} exact path={`/invest/horizon`} component={InvestmentHorizon} />
      <ProtectedRoutes bottom_tab={false} exact path={`/invest/mode_of_investment`} component={ModeOfInvestment} />

    </Switch>
  );
};

export default HomeLayout;
