import React from 'react';
import { Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import db from '../../../../../../libs/db';
import AdminServices from '../../../../../../services/api/AdminServices';
import BottomBackNextBtn from '../../../../components/atoms/BottomBackNextBtn';
import { dropdownCustom } from '../../../../components/atoms/DataCustomizer';
import ProfileCompleteHeader from '../../../../components/molecules/Headers/ProfileCompletionHeader';
import cssVar from '../../../../styles';


export default function Address(props) {

    const [stateList, setstateList] = React.useState([]);

    const [line1, setLine1] = React.useState('');
    const [line2, setLine2] = React.useState('');
    const [pinCode, setpinCode] = React.useState('');
    const [city, setCity] = React.useState('');
    const [state, setstate] = React.useState({});
    const [selectedItem, setselectedItem] = React.useState([]);

    const [loading, setloading] = React.useState(true);
    const [nextdisabled, setnextDisabled] = React.useState(true);
    const [value, setValue] = React.useState('');

    // helper
    const [helperType, sethelperType] = React.useState('');
    const [helperColor, sethelperColor] = React.useState('');
    const [helperText, sethelperText] = React.useState('');

    const checkOnBlur = (data) => {
        if (data.length !== 6 || isNaN(data)) {
            // 
            sethelperType('pin');
            sethelperColor('red');
            sethelperText('Enter a valid pin code');
        } else {
            // console.log('f000dgs');

            sethelperType('');
            sethelperColor('');
            sethelperText('');
        }
    };

    const dataAdult = {
        head: 'Address'
    };

    const dataMinor = {
        head: 'Postal Address'
    };

    const dataNri = {
        head: 'Indian Address'
    };

    const checkValidation = () => {
        if (
            line1.length !== 0
            && pinCode.length === 6
            && city.length !== 0
            && Object.entries(state).length !== 0
        ) {
            setnextDisabled(false);
        } else {
            setnextDisabled(true);
        }
    };

    const getData = async () => {
        const res = await AdminServices.getStates().then((res) => res);
        // console.log('DATA : ', res);
        const data = await dropdownCustom(res.data, ['UID', 'StateName']);
        //console.log(data);
        setstateList(data);
    };

    React.useEffect(() => {
        checkValidation();
    });

    React.useEffect(() => {
        db.get(db.model.userData).then((u) => {
            if (u) {
                // u = JSON.parse(u);
                console.log(u)
                if (u.indAdd) {
                    setLine1(u.indAdd.line1 || '');
                    setLine2(u.indAdd.line2 || '');
                    setpinCode(u.indAdd.pincode || '');
                    setCity(u.indAdd.city || '');
                    setstate(u.indAdd.state || '');
                    setstateList(u.indAdd.allstate || []);
                    setloading(false);
                } else {
                    getData().then(() => {
                        setloading(false);
                    });
                }
            }
        });

        checkValidation();
    }, []);

    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            {props.location.state.selection === 'adult' ? (
                <ProfileCompleteHeader
                    header={dataAdult.head}
                    percentage={30}
                    navigation={props.history} />
            ) : props.location.state.selection === 'minor' ? (
                <ProfileCompleteHeader
                    header={dataMinor.head}
                    percentage={30}
                    navigation={props.history} />
            ) : (
                <ProfileCompleteHeader
                    header={dataNri.head}
                    percentage={30}
                    navigation={props.history} />
            )}
            <div className="mt-2 mb-2">
                <div className="floating-form">
                    {/* <div className="floating-label">
                        <input
                            className="floating-input"
                            type="text"
                            id="line1"
                            placeholder=" "
                            onBlur={() => {
                                if (line1.length === 0) {
                                    sethelperType('line1');
                                    sethelperColor('red');
                                    sethelperText('Line 1 can\'t be empty');
                                } else {
                                    sethelperType('');
                                    sethelperColor('');
                                    sethelperText('');
                                }
                            }}
                            // error={helperType === 'line1'}
                            autoCapitalize="words"
                            value={line1}
                            onChange={(val) => {
                                setLine1(val.target.value)
                                console.log(val.target.value)
                            }}
                        />
                        <label className="floating-label-input" htmlFor="line1">Line 1</label>
                    </div> */}
                    <FloatingTextInput
                        type="text"
                        id="line1"
                        label="Line 1"
                        onChange={(val) => {
                            setLine1(val.target.value)
                        }}
                        onBlur={() => {
                            if (line1.length === 0) {
                                sethelperType('line1');
                                sethelperColor('red');
                                sethelperText('Line 1 can\'t be empty');
                            } else {
                                sethelperType('');
                                sethelperColor('');
                                sethelperText('');
                            }
                        }}
                        autoCapitalize="words"
                        value={line1}
                    />
                    {helperType === 'line1' ? (
                        <text style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                            {helperText}
                        </text>
                    ) : null}

                    {/* <div className="floating-label">
                        <input
                            className="floating-input"
                            type="text"
                            id="line2"
                            placeholder=" "
                            autoCapitalize="words"
                            value={line2}
                            onChange={(val) => {
                                setLine2(val.target.value)
                                console.log(val.target.value)
                            }}
                        />
                        <label className="floating-label-input" htmlFor="line2">Line 2</label>
                    </div> */}

                    <FloatingTextInput
                        type="text"
                        id="line2"
                        label="Line 2"
                        autoCapitalize="words"
                        value={line2}
                        onChange={(val) => {
                            setLine2(val.target.value)
                            console.log(val.target.value)
                        }}
                    />
                    {/* <div className="floating-label">
                        <input
                            className="floating-input"
                            type="number"
                            id="pin"
                            placeholder=" "
                            autoCapitalize="words"
                            maxLength={6}
                            // error={helperType === 'pin'}
                            value={pinCode}
                            onBlur={() => checkOnBlur(pinCode)}
                            onChange={(val) => {
                                setpinCode(val.target.value)
                                console.log(val.target.value)
                            }}
                        />
                        <label className="floating-label-input" htmlFor="pin">PIN code</label>
                    </div> */}

                    <FloatingTextInput
                        type="number"
                        id="pin"
                        label="PIN code"
                        maxLength={6}
                        // error={helperType === 'pin'}
                        value={pinCode}
                        onBlur={() => checkOnBlur(pinCode)}
                        onChange={(val) => {
                            setpinCode(val.target.value)
                            console.log(val.target.value)
                        }}
                    />

                    {helperType === 'pin' ? (
                        <text style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                            {helperText}
                        </text>
                    ) : null}

                    <div className="floating-label">
                        <input
                            className="floating-input"
                            type="text"
                            id="city"
                            placeholder=" "
                            onBlur={() => {
                                if (city.length === 0) {
                                    sethelperType('city');
                                    sethelperColor('red');
                                    sethelperText('City can\'t be empty');
                                } else {
                                    sethelperType('');
                                    sethelperColor('');
                                    sethelperText('');
                                }
                            }}
                            autoCapitalize="words"
                            value={city}
                            onChange={(val) => {
                                setCity(val.target.value)
                                console.log(val.target.value)
                            }}
                        />
                        <label className="floating-label-input" htmlFor="city">City</label>
                    </div>
                    {helperType === 'city' ? (
                        <text style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                            {helperText}
                        </text>
                    ) : null}

                    {/* <div className="dropdown">
                        <input
                            className="dropdown-select"
                            list="states"
                            name="state"
                            placeholder="State"
                            onChange={(item) => {
                                setstate(item)
                                console.log(item)
                            }}
                        />
                        <datalist id="states" >
                            {stateList.map((data) => {
                                return <option itemID={data.id} >{data.name}</option>
                            })}
                        </datalist>
                    </div> */}
                    <CustomDropDown
                        placeholder="Enter state name"
                        label="State"
                        initial={state}
                        items={stateList}
                        onChange={(item) => setstate(item)} />
                </div>
            </div>
            <div className="footer">
                {props.location.state.selection === 'adult' ? (
                    <BottomBackNextBtn
                        navigation={props.history}
                        next="/complete_pro/fatca"
                        params={props.location.state}
                        saveData={{
                            indAdd: {
                                line1,
                                line2,
                                pincode: pinCode,
                                city,
                                state,
                                allstate: stateList,
                                loadData: false
                            }
                        }}
                        nextdisabled={nextdisabled} />
                ) : props.location.state.selection === 'minor' ? (
                    <BottomBackNextBtn
                        navigation={props.history}
                        next="/complete_pro/fatca"
                        params={props.location.state}
                        saveData={{
                            indAdd: {
                                line1,
                                line2,
                                pincode: pinCode,
                                city,
                                state,
                                allstate: stateList,
                                loadData: false
                            }
                        }}
                        nextdisabled={nextdisabled} />
                ) : (
                    <BottomBackNextBtn
                        navigation={props.history}
                        next="/complete_pro/faddress"
                        params={props.location.state}
                        saveData={{
                            indAdd: {
                                line1,
                                line2,
                                pincode: pinCode,
                                city,
                                state,
                                allstate: stateList,
                                loadData: false
                            }
                        }}
                        nextdisabled={props.location.state.selection === 'nri' ? false : nextdisabled} />
                )}
            </div>
        </div>
    }
}

