import React from 'react';


export default function Accordion(props) {
  const { children, title, left, right = () => {},
          style, titleStyle, dividerStyle, secTitle,
          active = false } = props;
  const [expended, setExpended] = React.useState(active);


  // React.useEffect(() => { 
  // }, []);

  const toggleExpand = () => {
    // LayoutAnimation.configureNext(LayoutAnimation.Presets.easeInEaseOut);
    setExpended((e) => !e);
  };

  return (
    <div>
      <div style={{display: 'flex', flexDirection: 'row'}}
        onClick={toggleExpand}
        // android_ripple={{ color: '#EEE' }}
        >
        <div style={{display: 'flex', paddingTop: 10, flexDirection: 'row', alignItems: 'center', justifyContent: 'space-between', width: '100%'}}>
          <div >
            {left}
            <text>
              {title}
              {expended ? "" :  secTitle}
            </text>
          </div>
          {right(expended)}
        </div>

      </div>
      {expended && (
        <div>
          {children}
        </div>
      )}
    </div>
  );
}
